* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
  background-color: #adadad;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 20px;
  gap: 45px;

  &_title {
    margin: 10px;
  }

  &_inputs {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: flex-end;
  }
}
